import { getStroke } from 'perfect-freehand'

/**
 * https://github.com/tldraw/tldraw/blob/main/examples/core-example-advanced/src/shapes/pencil/pencil-helpers.ts
 */
function getComponentSvgPath(points, options = { size: 8 }) {
  const stroke = getStroke(points, options)

  if (!stroke.length) return ''

  const d = stroke.reduce(
    (acc, [x0, y0], i, arr) => {
      const [x1, y1] = arr[(i + 1) % arr.length]
      acc.push(x0, y0, (x0 + x1) / 2, (y0 + y1) / 2)
      return acc
    },
    ['M', ...stroke[0], 'Q']
  )

  d.push('Z')
  return d.join(' ')
}

export default getComponentSvgPath;